@import 'colors';
@import 'mixins';

.icons-section {
  margin-top: 35px;
  background: url('../img/bg2.png') no-repeat;
  background-size: cover;
  min-height: 662px;
  color: lighten($desc, 40%);
  &__menu-img {
    position: absolute;
    top: -30px;
    right: 30px;
    width: 290px; }
  .title {
    font-family: 'ms100'; }
  &__inner {
    width: 58%;
    float: left;
    padding-top: 90px;
    flex-direction: column;
    align-items: center; }
  &__content {
    width: 57%; }
  &__icons {
    padding-top: 85px;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center; }
  .icon {
    flex: 1 1 35px;
    max-width: 35px;
    width: 35px;
    height: 25px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    margin: 0 20px 65px; } }

